<template>
  <div class="table-list-page">
    <div class="content-header">
      <bread-crumb :current-level="isCompleted ? '已补充' : '待补充'" />
    </div>
    <div class="content-body">
      <div class="action-group">
        <action-group
          :query="formData"
          @search="handleSearch"
        />
      </div>

      <data-table
        :loading="isBusinessLoading"
        :table-props="tableProps"
        :pagination-props="paginationProps"
        @selection-change="handleSelectionChange"
        @page-change="handlePageChange"
        @page-size-change="handlePageSizeChange"
      >
        <el-table-column
          type="selection"
          width="55"
        />
        <el-table-column
          prop="ticketNumber"
          label="TicketNum"
        />
        <el-table-column
          prop="name"
          label="业务对象名称"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            <a
              title="查看业务对象名称详情"
              href="javascript:void(0);"
              @click="toDetail(scope.row)"
            >
              {{ scope.row.name }}
            </a>
          </template>
        </el-table-column>
        <el-table-column
          prop="level2Category"
          label="业务部门"
          show-overflow-tooltip
        >
          <template slot-scope="scope">
            {{ scope.row.level2Category || '-' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="submitter"
          label="提交人"
        />
        <el-table-column
          prop="submissionTime"
          label="提交时间"
          show-overflow-tooltip
        />
        <el-table-column
          prop="businessStatus"
          label="业务信息状态"
        >
          <template slot-scope="scope">
            <span :class="{'status-text': true, 'status-finished': scope.row.businessStatus === '1'}">
              {{ scope.row.businessStatus === '1' ? '已补充' : '待补充' }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="technicalStatus"
          label="技术信息状态"
        >
          <template slot-scope="scope">
            <span :class="{'status-text': true, 'status-finished': scope.row.technicalStatus === '1'}">
              {{ scope.row.technicalStatus === '1' ? '已补充' : '待补充' }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
        >
          <template slot-scope="scope">
            <gs-button
              type="text-primary"
              @click="toDetail(scope.row)"
            >详情</gs-button>
          </template>
        </el-table-column>
      </data-table>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import ActionGroup from './action-group';
import BreadCrumb from 'src/components/breadcrumb';
import {
  checkLoading
} from 'src/store/status';
import './style';

export default {
  components: {
    DataTable,
    ActionGroup,
    BreadCrumb
  },

  data() {
    return {
      page: 1,
      pageSize: 10,
      selecteds: [],
      formData: {},
      searchMode: false
    };
  },

  computed: {
    ...mapState('business', {
      management: 'management'
    }),

    tableData() {
      const {
        management
      } = this;
      let data = [];
      data = management;

      return data;
    },
    tableProps() {
      const {
        tableData
      } = this;

      return {
        total: tableData.total,
        data: tableData.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total
      };
    },
    isBusinessLoading() {
      return checkLoading(this.management.status);
    },
    isCompleted() {
      return this.$route.name === 'supplementalList';
    },
    commonParams() {
      const {
        page,
        pageSize
      } = this;
      // const sortMap = {
      //   ascending: 'asc',
      //   descending: 'desc'
      // };

      return {
        offset: (page - 1) * pageSize,
        limit: pageSize,
        status: this.isCompleted ? 'added' : 'blank'
      };
    }
  },

  watch: {
    isCompleted(val) {
      this.queryList();
      this.formData = {};
    }
  },

  mounted() {
    this.queryList();
  },

  methods: {
    ...mapActions('business', [
      'getManageTableList'
    ]),

    queryList() {
      if (this.searchMode) {
        this.searchMode = false;
        this.page = 1;
      }
      this.getManageTableList({
        ...this.commonParams
      });
    },

    findList() {
      this.getManageTableList({
        ...this.formData,
        ...this.commonParams
      });
    },

    handleSearch(params) {
      this.formData = params;
      this.page = 1;

      if (params.ticketNumber || params.name || params.level2CategoryId || params.submitter) {
        this.searchMode = true;
        this.findList();
      } else {
        this.searchMode = false;
        this.queryList();
      }
    },

    queryOrFindList() {
      if (this.searchMode) {
        this.findList();
      } else {
        this.queryList();
      }
    },

    handlePageChange(page) {
      this.page = page;
      this.queryOrFindList();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.queryOrFindList();
    },

    /**
     * 关联列表复选框change event
     */
    handleSelectionChange(selecteds) {
      this.selecteds = selecteds;
    },

    toDetail(row) {
      const {
        isCompleted
      } = this;
      this.$router.push({
        path: isCompleted
          ? `/main/business-manage/supplemental/detail/${row.businessId}`
          : `/main/business-manage/unsupplemental/detail/${row.businessId}`,
        query: {dp: row.level2Category}
      });
    }
  }
};
</script>




