<template>
  <div class="query-form">
    <gs-form
      ref="queryForm"
      :model="params"
      inline
    >
      <gs-form-item
        label="TicketNum"
        prop="TickNum"
      >
        <gs-input
          v-model="params.ticketNumber"
          placeholder="请输入TicketNum"
        />
      </gs-form-item>

      <gs-form-item
        label="业务对象名称"
        prop="name"
      >
        <gs-input
          v-model="params.name"
          placeholder="请输入业务对象名称"
        />
      </gs-form-item>

      <gs-form-item
        label="业务部门"
        prop="level2CategoryId"
      >
        <gs-select
          v-model="params.level2CategoryId"
          placeholder="请选择业务部门"
          clearable
        >
          <gs-option
            v-for="item in departments.list"
            :key="item.guid"
            :label="item.name"
            :value="item.guid"
          />
        </gs-select>
      </gs-form-item>

      <gs-form-item
        label="提交人"
        prop="submitter"
      >
        <gs-input
          v-model="params.submitter"
          placeholder="请输入提交人"
        />
      </gs-form-item>

      <gs-form-item>
        <gs-button
          type="primary"
          @click="handleSearch"
        >搜索</gs-button>
      </gs-form-item>
    </gs-form>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';

export default {
  props: {
    query: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    return {
      params: {
        ...this.query
      }
    };
  },

  computed: {
    ...mapState('business', {
      departments: 'departments'
    })
  },

  watch: {
    query: {
      handler() {
        this.params = {...this.query};
      },
      deep: true
    }
  },

  mounted() {
    this.getDepartments();
  },

  methods: {
    ...mapActions('business', [
      'getDepartments'
    ]),
    handleSearch() {
      this.$emit('search', {
        ...this.params
      });
    }
  }
};
</script>




